import DeleteOutlined from "@mui/icons-material/DeleteOutlined";
import EditOutlined from "@mui/icons-material/EditOutlined";
import { Box, ListItemButton, ListItemText, Typography } from "@mui/material";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useSearchParams } from "react-router-dom";
import { useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveTable from "../../../../components/ResponsiveTable";
import Text from "../../../../components/Text";
import pricify from "../../../../utils/pricify";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";
import useGetRowNumber from "../../../../utils/useGetRowNumber";
import useNavigateTask from "../../../../utils/useNavigateTask";
import Utils from "../../Utils";


const InvoicesTable = ({
    loading,
    data,
    meta,
    infiniteScrollProps,
    onClickEdit,
    onClickDelete,
}) => {
    const { inject } = useAuth();
    const { t } = useTranslation(["global", "invoices"]);

    const [searchParams, setSearchParams] = useSearchParams();

    const [sort, setSort] = useState(
        searchParams.has('sort') ? searchParams.get('sort') : '-createdAt'
    );

    useAfterFirstRender(() => {
        setSearchParams({
            ...Object.fromEntries(searchParams),
            sort
        })
    }, [sort]);

    const getRowNumber = useGetRowNumber();

    const navigateTask = useNavigateTask();
    const taskDetails = useTask('Invoices.details');


    return (
        <>
            <ResponsiveTable
                data={data}
                loading={loading}
                infiniteScrollProps={infiniteScrollProps}
                definitions={sortBy([
                    {
                        title: 'ردیف',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        renderCell: (row, index) => <Text>{getRowNumber(index)}</Text>,
                        priority: 100
                    }, {
                        field: 'number',
                        title: 'شماره',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        render: row => <Text>{row.number}</Text> || '-',
                        priority: 200
                    }, {
                        title: 'مشتری',
                        rowCellProps: {
                            width: 400
                        },
                        renderCell: row => (
                            <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                <strong><Text>{row.customer?.fullName}</Text></strong>
                            </Box>
                        ),
                        priority: 300
                    }, {
                        title: <>{t("خالص فاکتور")} <Typography variant="caption">(ریال)</Typography></>,
                        rowCellProps: {
                            width: 400
                        },
                        renderCell: row => {
                            const total = Utils.getInvoiceTotal(row.items, row.factors);
                            return isFinite(total) ? pricify(total) : 0;
                        },
                        priority: 300
                    },
                    ...flatten((inject('Invoices', 'InvoicesList.definitions') || []))
                ], o => o.priority)}
                renderSheetTitle={row => <Text>{row.name}</Text>}
                renderListItemChildren={(row) => (
                    <ListItemButton
                        onClick={() => navigateTask(taskDetails(row))}
                    >
                        {/* <ListItemAvatar>
                            <Avatar src={row.avatar} />
                        </ListItemAvatar> */}
                        <ListItemText
                            primary={<strong>{row.name}</strong>}
                        // secondary={row.roles && row.roles.length > 0 ? <Text>{row.roles.map(r => r.name).join(', ')}</Text> : '-'}
                        />
                    </ListItemButton>
                )}

                tableRowProps={row => ({
                    hover: true,
                    striped: true,
                    onClick: () => navigateTask(taskDetails(row))
                })}

                paginationProps={{
                    count: meta.total,
                    rowsPerPage: searchParams.has('perPage') ? Number(searchParams.get('perPage')) : 25,
                    page: searchParams.has('page') ? Number(searchParams.get('page')) : 0,
                    onPageChange: (e, page) => setSearchParams({ ...Object.fromEntries([...searchParams]), page }),
                    onRowsPerPageChange: e => setSearchParams({ ...Object.fromEntries([...searchParams]), perPage: e.target.value })
                }}

                actions={[
                    {
                        title: 'ویرایش',
                        icon: <EditOutlined />,
                        onClick: onClickEdit,
                        authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('invoice:edit')
                    },
                    {
                        title: 'حذف',
                        icon: <DeleteOutlined />,
                        onClick: invoice => onClickDelete(invoice, false),
                        authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('invoice:delete')
                    }
                ]}
            />
        </>
    )
}

export default InvoicesTable